.page-integral {
	.title {
		height: 90px;
		box-sizing: border-box;
		color: #fff;
		font-size: 32px;
		text-align: center;
		background-color: #f75455;
		padding-top: 20px;
	}
	.integral-list {
		margin-top: 15px;
		background-color: #fff;
		color: #333;
		height: calc(~"100% - 105px");
		.row.row-title {
			color: #999;
			height: 32px;
			font-size: 12px;
			border-bottom: 1px solid #f2f3f5;
			width: 100%;
			box-sizing: border-box;
			padding: 0 10px 0 16px;
			align-items: center;
			.time {
				text-align: center;
			}
		}
		.list-content {
			font-size: 14px;
			height: calc(~"100% - 32px");
			overflow-y: auto;
			.row {
				height: 61px;
				box-sizing: border-box;
				padding: 0 10px 0 16px;
				align-items: center;
				border-bottom: 1px solid #f2f3f5;
				width: 100%;
			}
			.time {
				text-align: center;
			}
			.hour {
				font-size: 12px;
				color: #999;
			}
		}
	}
}

